#nav-overlay {
  background-color: rgba(0, 0, 0, 0.2);
  transition: opacity 300ms cubic-bezier(0.645, 0.045, 0.355, 1);
  opacity: 0;
  height: 0;
}
#nav-overlay:not(.is-invisible) {
  position: fixed;
  opacity: 1;
  height: 100%;
  z-index: 5;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
